form {
  display: flex;
  width: 100%;
  flex-direction: column;
  height: 70%;
  font-size: 1.3vmax;
}

form > p {
  font-size: 1.3vmax;
  margin-bottom: 1vmax;
}

form > h2 {
  margin: 1% 0 3% 0;
  font-size: 1.6vmax;
}

form label {
  margin: 5% 0 3% 0;
  font-size: 1.1vmax;
}

form input[type="text"] {
  font-size: 1vmax;
  color: #c5c6c7;
  background-color: #1f2833;
  border: 2px solid #66fcf1;
  padding: 2%;
  border-radius: 8px;
}

form input[type="text"]::placeholder {
  color: #c5c6c7;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

form textarea {
  font-size: 1vmax;
  color: #c5c6c7;
  background-color: #1f2833;
  border: 2px solid #66fcf1;
  padding: 2%;
  border-radius: 8px;
  resize: none;
  /* height: 15vmax; */
}

form textarea::placeholder {
  color: #c5c6c7;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

form input[type="submit"] {
  margin: 5% 0;
  font-size: 1vmax;
  font-weight: 500;
  border: none;
  border-radius: 5px;
  padding: 0.4vmax 0;
  background-color: #0b0c10;
  color: #c5c6c7;
  border: 2px solid #66fcf1;
  transition: all 0.3s;
  padding: 1.5%;
}

form input[type="submit"]:hover {
  color: #0b0c10;
  background-color: #c5c6c7;
  border: #c5c6c7;
  padding: 1.5%;
}
