.myNotes {
  padding: 3%;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.noteTable {
  width: 100%;
  height: 100%;
}

.dataGridHeader {
  color: #66fcf1;
  font-size: 1vmax;
  margin-left: 0.5vmax;
}

.dataGridRowHighlighted {
  color: #66fcf1;
  font-size: 1vmax;
}

.dataGridRow {
  color: #c5c6c7;
  background-color: #1f2833;
  font-size: 1vmax;
}

.myNotes a {
  padding: 0.3vmax 1vmax;
}
.myNotes button {
  padding: 0.5vmax;
  margin-left: 12px;
}

.myNotes a svg {
  color: rgb(19, 125, 19);
}
.myNotes a:hover svg {
  color: rgb(10, 81, 10);
}
.myNotes button svg {
  color: rgb(223, 27, 27);
}
.myNotes button:hover svg {
  color: rgb(160, 26, 26);
}
