.registerContainer {
  width: 40%;
  height: 60%;
  margin: auto;
  padding: 0 5%;
}

.registerForm {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-around;
}

.registerForm > h2 {
  font-size: 2.5vmax;
  margin: 0% 0 7% 0;
  text-align: center;
  text-decoration: underline;
}

.registerForm input {
  font-size: 1vmax;
  padding: 2%;
}

.registerForm a {
  text-align: end;
  font-size: 1.1vmax;
  color: #66fcf1;
  text-decoration: none;
  margin-top: 5px;
  transition: all 0.2s;
}

.registerForm a:hover {
  color: #45a29e;
}

.inputReg {
  font-size: 1vmax;
  color: #c5c6c7 !important;
  background-color: #1f2833;
  border: 2px solid #66fcf1;
  padding: 2%;
  border-radius: 8px;
}

.inputReg::placeholder {
  color: #c5c6c7;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
